/* Hero Section Styles */

.hero {
  @include section(118px, 118px);
  max-width: 480px;
  color: var(--main-light-color);

  background-position: center;
  background-size: cover;
  background-color: var(--footer-background-color);

  // background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
  //   url('../images/hero/mob/hero_background.jpg');

  // @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  //   background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
  //     url('../images/hero/mob/hero_background@2x.jpg');
  // }

  // @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
  //   background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
  //     url('../images/hero/mob/hero_background@3x.jpg');
  // }

  @media screen and (min-width: 768px) {
    max-width: 768px;

    // background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
    //   url('../images/hero/tab/hero_background.jpg');

    // @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    //   background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
    //     url('../images/hero/tab/hero_background@2x.jpg');
    // }

    // @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
    //   background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
    //     url('../images/hero/tab/hero_background@3x.jpg');
    // }
  }

  @media screen and (min-width: 1200px) {
    @include section(200px, 200px);
    max-width: 1600px;

    // background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
    //   url('../images/hero/desk/hero_background.jpg');

    // @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    //   background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
    //     url('../images/hero/desk/hero_background@2x.jpg');
    // }

    // @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
    //   background-image: linear-gradient(var(--hero-gradient-color), var(--hero-gradient-color)),
    //     url('../images/hero/desk/hero_background@3x.jpg');
    // }
  }
}

.hero__wrapper {
  max-width: 360px;
  margin: 0 auto;
  text-align: center;

  @media screen and (min-width: 1200px) {
    max-width: 696px;
  }
}

.hero__title {
  margin-bottom: 30px;

  font-size: 26px;
  line-height: 1.62;
  font-weight: 900;
  letter-spacing: 0.06em;

  text-align: center;
  text-transform: uppercase;

  @media screen and (min-width: 1200px) {
    font-size: 44px;
    line-height: 1.36;
  }
}
